.navbar-nav .border-bottom:last-child {
  border-bottom: none!important;
}

@media (min-width: 576px) {
  .navbar-collapse .border-bottom {
    border-bottom: none!important;
  }
}

.navbar-toggler, .navbar-nav {
  z-index: 2;
}

.collapse.show .backdrop {
  display: block;
  z-index: 1;
}